.button {
  width: 150px;
  height: 50px;
  background-color: #5035ff;
  border: none;
  border-radius: 5px;
  color: white;
  font-weight: 700;
  font-size: 1.3rem;
  cursor: pointer;
  font-family: 'Karla', sans-serif;
  transition: 0.2s;
}

.button:hover {
  border-radius: 10px;
}
